import React, { Component } from 'react';
// import Sidebar from './components/sidebar';
import Home from './components/home';
import Projects from './components/projects';
import About from './components/about';
import Contact from './components/contact';
import MenuItems from './components/menu-items';

import './main.css';

class App extends Component {
  log = (data) => {
    let value = data.item;
    this.setState({
      option: value
    })
  }
  state = {
    option: 'Home',
    displayed: 'retract',
    caret: 'fa-caret-left',
    width: '25vw',
    rightCaret: 'none'
  }
  changeDisplay = () => {
    this.setState({
      displayed: 'extends'
    })
  }
  showDisplay = () => {
    if (this.state.width === '25vw') {
      this.setState({ width: '0vw', rightCaret: 'block' });

    } else {
      this.setState({ width: '25vw', rightCaret: 'none' })
    }

  }

  render() {
    switch (this.state.option) {
      case 'Home':
        return (<div className="mainApp">
          <div className="normal-way">
            <div style={{ width: this.state.width }} className="sidebar">
              <div className="extends-retract">
                <span onClick={this.showDisplay} className={`fa ${this.state.caret} ${this.state.displayed}`}></span>
              </div>
              <div className="Logo"></div>
              <div className="Menu">
                <MenuItems handleProps={this.log} item="Home" />
                <MenuItems handleProps={this.log} item="Projects" />
                <MenuItems handleProps={this.log} item="About" />
                {/* <MenuItems handleProps={this.log} item="Contact" /> */}
              </div>
            </div>
            <div style={{ position: 'relative', display: this.state.rightCaret }} className="extends-retract">
              <span onClick={this.showDisplay} className={`fa fa-caret-right extends`}></span>
            </div>
            <Home />
          </div>
          <div className="responsive-way">
            <Home />
            <Projects />
            <About/>
            {/* <Contact/> */}
          </div>

        </div>);
      // break;
      case 'Projects':
          return (<div className="mainApp">
          <div className="normal-way">
            <div style={{ width: this.state.width }} className="sidebar">
              <div className="extends-retract">
                <span onClick={this.showDisplay} className={`fa ${this.state.caret} ${this.state.displayed}`}></span>
              </div>
              <div className="Logo"></div>
              <div className="Menu">
                <MenuItems handleProps={this.log} item="Home" />
                <MenuItems handleProps={this.log} item="Projects" />
                <MenuItems handleProps={this.log} item="About" />
                {/* <MenuItems handleProps={this.log} item="Contact" /> */}
              </div>
            </div>
            <div style={{ position: 'relative', display: this.state.rightCaret }} className="extends-retract">
              <span onClick={this.showDisplay} className={`fa fa-caret-right extends`}></span>
            </div>
            <Projects />
          </div>
          <div className="responsive-way">
            <Home />
            <Projects />
            <About/>
            {/* <Contact/> */}
          </div>

        </div>);
      // break;
      case 'About':
          return (<div className="mainApp">
          <div className="normal-way">
            <div style={{ width: this.state.width }} className="sidebar">
              <div className="extends-retract">
                <span onClick={this.showDisplay} className={`fa ${this.state.caret} ${this.state.displayed}`}></span>
              </div>
              <div className="Logo"></div>
              <div className="Menu">
                <MenuItems handleProps={this.log} item="Home" />
                <MenuItems handleProps={this.log} item="Projects" />
                <MenuItems handleProps={this.log} item="About" />
                {/* <MenuItems handleProps={this.log} item="Contact" /> */}
              </div>
            </div>
            <div style={{ position: 'relative', display: this.state.rightCaret }} className="extends-retract">
              <span onClick={this.showDisplay} className={`fa fa-caret-right extends`}></span>
            </div>
            <About />
          </div>
          <div className="responsive-way">
            <Home />
            <Projects />
            <About/>
            {/* <Contact/> */}
          </div>

        </div>);
      // break;
      case 'Contact':
          return (<div className="mainApp">
          <div className="normal-way">
            <div style={{ width: this.state.width }} className="sidebar">
              <div className="extends-retract">
                <span onClick={this.showDisplay} className={`fa ${this.state.caret} ${this.state.displayed}`}></span>
              </div>
              <div className="Logo"></div>
              <div className="Menu">
                <MenuItems handleProps={this.log} item="Home" />
                <MenuItems handleProps={this.log} item="Projects" />
                <MenuItems handleProps={this.log} item="About" />
                {/* <MenuItems handleProps={this.log} item="Contact" /> */}
              </div>
            </div>
            <div style={{ position: 'relative', display: this.state.rightCaret }} className="extends-retract">
              <span onClick={this.showDisplay} className={`fa fa-caret-right extends`}></span>
            </div>
            <Contact />
          </div>
          <div className="responsive-way">
            <Home />
            <Projects />
            <About/>
            {/* <Contact/> */}
          </div>

        </div>);
      // break;
      default:
          return (<div className="mainApp">
          <div className="normal-way">
            <div style={{ width: this.state.width }} className="sidebar">
              <div className="extends-retract">
                <span onClick={this.showDisplay} className={`fa ${this.state.caret} ${this.state.displayed}`}></span>
              </div>
              <div className="Logo"></div>
              <div className="Menu">
                <MenuItems handleProps={this.log} item="Home" />
                <MenuItems handleProps={this.log} item="Projects" />
                <MenuItems handleProps={this.log} item="About" />
                {/* <MenuItems handleProps={this.log} item="Contact" /> */}
              </div>
            </div>
            <div style={{ position: 'relative', display: this.state.rightCaret }} className="extends-retract">
              <span onClick={this.showDisplay} className={`fa fa-caret-right extends`}></span>
            </div>
            <Home />
          </div>
          <div className="responsive-way">
            <Home />
            <Projects />
            <About/>
            {/* <Contact/> */}
          </div>

        </div>);
    }
  }
}

export default App;
