import React from 'react';
import './components.css';
function ProjectItem(props) {
    return (
        <div className="project-item">
            <div className="warp">
                <a rel="noopener noreferrer" target="_blank" href={props.link}>
                    <div className={`project-item-img item-element ${props.linkImg}`}></div>
                </a>
                <div className="project-item-desc item-element">
                    <a rel="noopener noreferrer" target="_blank" href={props.link} className="desc-element">
                        {props.titulo}
                    </a>
                    <h3 className="desc-element">Participation: {props.participacion}</h3>
                    <p className="desc-element">{props.desc}</p>
                </div>
            </div>

        </div>
    );
}
function Projects(props) {
    return (
        <div className="projects backgrounds">
            <h1>PROJECTS</h1>
            <br></br>
            <ProjectItem link="#" titulo="My portfolio"
                participacion="Creador" 
                desc="Portafolio creado con React.js" linkImg="img1"
            />
            <ProjectItem link="http://tiseguridad.tk" titulo="Tiseguridad" 
                participacion="Backend con Php. Desarrollo de base de datos" 
                desc="Pagina web para empresa dedicada a cursos
                de seguridad informática" linkImg="img2"
            />
            <ProjectItem link="https://github.com/Jamael17/Stickser" titulo="Stickser" 
                participacion="Creador" 
                desc="Extensión de Google Chrome para la personalización del
                navegador" linkImg="img3"
            />
            <ProjectItem link="https://jcurvelo.github.io/Fatimas" titulo="Fatima's website (En proceso)" 
                participacion="Creador" 
                desc="Web de ejemplo creada 100% con CSS y HTML nativo" linkImg="img4"
            />
        </div>

    );
}

export default Projects;