import React, { Component } from 'react';
import '../main.css';

class MenuItems extends Component {
    sendInfo = ()=>{
        this.props.handleProps(this.props)
    }
    render(){
        return (
            <div onClick={this.sendInfo} item="Home" className="Menu-item">{this.props.item}</div>
        );
    }

}
export default MenuItems;